:root {
  --font-body: "Open Sans", sans-serif;
  --font-heading: "Poppins", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --pink: hsl(322, 100%, 66%);
  --lt-pink: hsl(321, 100%, 78%);
  --lt-red: hsl(0, 100%, 63%);
  --very-dk-cyan: hsl(192, 100%, 9%);
  --very-pl-blue: hsl(207, 100%, 98%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
  font-family: var(--font-body);
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  background-color: var(--white);
  color: var(--very-dk-cyan);
  font-size: 0.875rem;
  font-weight: 400;
  text-align: center;
  line-height: 1.3;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-family: var(--font-heading);
  font-size: 3.3125rem;
  font-weight: 700;
  line-height: 1.5;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding: 0 2.5rem;
  max-width: 81.25rem;
  margin: 0 auto;
}
.container-md {
  max-width: 62.5rem;
  margin: 0 auto;
}

.flex {
  display: flex;
}
.flex-row {
  flex-direction: row;
}
.flex-col {
  flex-direction: column;
}
.flex-jc-sb {
  justify-content: space-between;
}
.flex-jc-c {
  justify-content: center;
}
.flex-ai-c {
  align-items: center;
}
.flex-ai-s {
  align-items: flex-start;
}

.nav {
  margin-bottom: 6.25rem;
  padding-top: 2.5rem;
  padding-bottom: 2.5rem;
}
@media (max-width: 39.9375em) {
  .nav {
    flex-direction: column;
    align-items: start;
    gap: 1.25rem;
  }
}
@media (min-width: 40em) {
  .nav {
    margin-bottom: 6.875rem;
    padding-top: 3rem;
    padding-bottom: 3rem;
  }
}
.nav__logo {
  display: inline-block;
  font-size: 0;
}
.nav__logo img {
  width: 8.75rem;
  height: 1.5rem;
}
@media (min-width: 40em) {
  .nav__logo img {
    width: 10.3125rem;
    height: 1.75rem;
  }
}
@media (min-width: 71.875em) {
  .nav__logo img {
    width: 14.6875rem;
    height: 2.3125rem;
  }
}
.nav__btn {
  display: inline-block;
  border: 0.0625rem solid;
  border-radius: 3.125rem;
  padding: 0.375rem 1.25rem;
  font-size: 0.75rem;
  color: var(--lt-pink);
  transition: all 300ms ease-in-out;
}
.nav__btn:hover {
  border: 0.0625rem solid var(--lt-pink);
  background-color: var(--lt-pink);
  color: var(--white);
}
@media (max-width: 39.9375em) {
  .nav__btn {
    border-radius: 3.125rem;
    padding: 0.25rem 0.9375rem;
    font-size: 0.625rem;
  }
}
@media (min-width: 40em) {
  .nav__btn {
    border-radius: 3.125rem;
    padding: 0.625rem 1.875rem;
    font-size: 1rem;
  }
}

.main > * {
  margin-bottom: 5rem;
}
@media (min-width: 40em) {
  .main > * {
    margin-bottom: 5.625rem;
  }
}
@media (min-width: 71.875em) {
  .main > * {
    margin-bottom: 6.25rem;
  }
}

.getstarted > h1 {
  font-family: var(--font-heading);
  font-weight: 700;
  font-size: 2rem;
}
@media (min-width: 40em) {
  .getstarted > h1 {
    margin-bottom: 1.875rem;
    font-size: 2.25rem;
  }
}
@media (min-width: 71.875em) {
  .getstarted > h1 {
    font-size: 2.75rem;
  }
}
.getstarted > p {
  margin-bottom: 3.75rem;
  line-height: 1.5;
}
@media (min-width: 40em) {
  .getstarted > p {
    max-width: 37.5rem;
    margin: 0 auto 3.75rem;
    font-size: 1.25rem;
  }
}
.getstarted > a {
  display: inline-block;
  padding: 1.125rem 3.125rem;
  border: 0.0625rem solid var(--pink);
  border-radius: 5rem;
  font-size: 0.875rem;
  font-weight: 600;
  color: var(--white);
  background-color: var(--pink);
  transition: all 300ms ease-in-out;
}
.getstarted > a:hover {
  border: 0.0625rem solid var(--pink);
  color: var(--pink);
  background-color: var(--white);
}
@media (min-width: 40em) {
  .getstarted > a {
    padding: 1.25rem 5rem;
    border-radius: 5rem;
    font-size: 1.375rem;
  }
}
.getstarted > span {
  display: block;
  font-size: 2.1875rem;
  font-family: var(--font-heading);
  font-weight: 700;
  margin-bottom: 1.875rem;
}
@media (min-width: 40em) {
  .getstarted > span {
    font-size: 1.875rem;
  }
}
@media (min-width: 71.875em) {
  .getstarted > span {
    font-size: 2.1875rem;
  }
}

.mockup__img {
  width: 100%;
}

.engagement {
  gap: 5.625rem;
}
@media (min-width: 40em) {
  .engagement {
    flex-direction: row;
    justify-content: space-around;
  }
}
.engagement > * {
  min-width: 11.25rem;
}
.engagement__icon {
  display: block;
  margin-bottom: 0.625rem;
  margin-left: 0.3125rem;
}
.engagement__data {
  display: block;
  margin-bottom: 0.3125rem;
  font-family: var(--font-heading);
  font-size: 3.75rem;
  font-weight: 700;
}
@media (min-width: 40em) {
  .engagement__data {
    font-size: 5rem;
  }
}
@media (min-width: 71.875em) {
  .engagement__data {
    font-size: 6.25rem;
  }
}

.illustration {
  padding-top: 3.125rem;
  gap: 5rem;
}
.illustration__img {
  width: 100%;
}
.illustration__header {
  font-size: 1.5625rem;
  font-family: var(--font-heading);
}
@media (min-width: 40em) {
  .illustration__header {
    font-size: 1.875rem;
  }
}
@media (min-width: 40em) {
  .illustration__content {
    font-size: 1rem;
  }
}
@media (min-width: 40em) {
  .illustration {
    gap: 5.625rem;
    flex-direction: row;
    text-align: left;
  }
  .illustration-item1 {
    order: 2;
  }
  .illustration-item2 {
    order: 1;
  }
}
.illustration-sec {
  margin-bottom: 5rem;
}
@media (min-width: 40em) {
  .illustration-sec {
    margin-bottom: 5.625rem;
  }
}
@media (min-width: 71.875em) {
  .illustration-sec {
    margin-bottom: 6.25rem;
  }
}

.bg-color {
  background-color: var(--very-pl-blue);
}

.bg-1-top {
  height: 6.25rem;
  background-image: url("../images/bg-section-top-mobile-1.svg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 100% 100%;
}
@media (min-width: 40em) {
  .bg-1-top {
    background-image: url("../images/bg-section-top-desktop-1.svg");
  }
}
@media (min-width: 71.875em) {
  .bg-1-top {
    height: 7.5rem;
  }
}

.bg-1-bottom {
  height: 3.75rem;
  background-image: url("../images/bg-section-bottom-mobile-1.svg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 100% 100%;
  margin-bottom: 5rem;
}
@media (min-width: 40em) {
  .bg-1-bottom {
    margin-bottom: 5.625rem;
    height: 6.25rem;
    background-image: url("../images/bg-section-bottom-desktop-1.svg");
  }
}
@media (min-width: 71.875em) {
  .bg-1-bottom {
    margin-bottom: 6.25rem;
    height: 9.375rem;
  }
}

.bg-2-top {
  height: 6.25rem;
  background-image: url("../images/bg-section-top-mobile-2.svg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 100% 100%;
}
@media (min-width: 40em) {
  .bg-2-top {
    background-image: url("../images/bg-section-top-desktop-2.svg");
  }
}
@media (min-width: 71.875em) {
  .bg-2-top {
    height: 7.5rem;
  }
}

.bg-2-bottom {
  height: 5rem;
  background-image: url("../images/bg-section-bottom-mobile-2.svg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 100% 100%;
  margin-bottom: 5rem;
}
@media (min-width: 40em) {
  .bg-2-bottom {
    margin-bottom: 5.625rem;
    height: 6.25rem;
    background-image: url("../images/bg-section-bottom-desktop-2.svg");
  }
}
@media (min-width: 71.875em) {
  .bg-2-bottom {
    margin-bottom: 6.25rem;
    height: 9.375rem;
  }
}

.footer-bg {
  height: 4.375rem;
  background-image: url("../images/bg-footer-top-mobile.svg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 100% 100%;
}
@media (min-width: 40em) {
  .footer-bg {
    height: 6.25rem;
    background: none;
    background-image: url("../images/bg-footer-top-desktop.svg");
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 100% 100%;
  }
}
@media (min-width: 71.875em) {
  .footer-bg {
    height: 8.125rem;
  }
}

footer {
  background-color: var(--very-dk-cyan);
}

.footer {
  padding-top: 5rem;
  padding-bottom: 3.125rem;
  gap: 5rem;
  color: var(--white);
  font-weight: 600;
  text-align: left;
}
.footer__logo {
  display: inline-block;
}
.footer__header {
  font-family: var(--font-heading);
  font-size: 1.25rem;
  text-transform: uppercase;
}
.footer__content {
  margin-bottom: 1.875rem;
  line-height: 1.8;
}
.footer-form {
  gap: 0.625rem;
}
@media (min-width: 71.875em) {
  .footer-form {
    flex-direction: row;
    gap: 1.875rem;
  }
}
.footer__label {
  display: none;
}
.footer__email {
  border-radius: 0.3125rem;
  border: none;
  padding: 0.9375rem;
  font-size: 1rem;
}
@media (min-width: 71.875em) {
  .footer__email {
    flex: 2;
  }
}
.footer__submit {
  color: var(--white);
  background-color: var(--pink);
  border-radius: 0.3125rem;
  border: none;
  padding: 0.9375rem;
  font-size: 1rem;
  font-weight: 400;
  transition: background-color 300ms ease-in-out;
}
.footer__submit:hover {
  cursor: pointer;
  background-color: var(--lt-pink);
}
@media (min-width: 71.875em) {
  .footer__submit {
    flex: 1;
  }
}
@media (min-width: 40em) {
  .footer {
    flex-direction: row;
    gap: 6.25rem;
  }
  .footer-item1 {
    order: 2;
  }
  .footer-item2 {
    order: 1;
  }
}
.footer__phonenum {
  display: block;
  margin-bottom: 0.625rem;
}
.footer__phonenum::before {
  content: url("../images/icon-phone.svg");
  margin-right: 1.25rem;
}
.footer__emailadd {
  display: block;
  margin-bottom: 1.875rem;
}
.footer__emailadd::before {
  content: url("../images/icon-email.svg");
  margin-right: 1.25rem;
}
.footer__links {
  display: inline-block;
  font-size: 1.875rem;
  color: var(--white);
}
.footer__links:not(:last-child) {
  margin-right: 0.9375rem;
}